*
{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}
.contact_container
{
    padding: 30px 40px;
    width: 100%;
    background: #f6f6f6;
}
.contact_container .contant
{
    padding: 10px 20px;
}
.contact_container .contant h2
{
    text-transform: uppercase;
    font-size: 32px;
    color: #010f1c;
    font-weight: 200;
}
.contact_container .contant .form
{
    margin-top: 30px;
    padding: 20px 30px;
    background: #fff;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.contact_container .contant .form form
{
    display: flex;
    flex-direction: column;
}
.contact_container .contant .form form input
{
    padding: 10px 30px;
    border: none;
    outline: none;
    background: none;
    border-bottom: 1px solid #010f1c;
    width: 350px;
    margin-top: 20px;
    margin-left: 35%;
}
.contact_container .contant .form form textarea
{
    padding: 10px 30px;
    border: none;
    outline: none;
    background: none;
    border-bottom: 1px solid #010f1c;
    max-width: 350px;
    margin-top: 20px;
    resize: none;
    margin-left: 35%;
}
.contact_container .contant .form form button
{
    margin-top: 30px;
    width: 100px;
    padding: 10px 0px;
    border: none;
    outline: none;
    background:none;
    margin-left: 35%;
    background: #010f1c;
    color: #fff;
    cursor: pointer;
}

@media (max-width:799px) {
    .contact_container .contant .form form input {
        width: 70%;
        margin-left: 15%;
    }
    .contact_container .contant .form form textarea {
        padding: 10px 30px;
        max-width: 70%;
        margin-left: 15%;
    }
    .contact_container .contant .form form button {
        width: 35%;
        padding: 10px 0px;
        margin-left: 15%;
        font-size: 24px;
    }
}