* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}

.free {
    padding: 10px 30px;
    width: 100%;
    background: #010f1c;
    display: flex;
}

.free .icon {
    color: #f3b123;
    font-size: 32px;
}

.free p {
    margin-left: 10px;
    color: #f6f7f9;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 600;
}

.logo .link {
    font-size: 2rem;
    text-decoration: none;
    /* font-style: italic; */
    font-family: cursive;
    letter-spacing: 5px;
    color: black;
    font-weight: 600;
    display: flex;
    align-items: center;
}

.logo span {
    color: red;
    font-family: cursive;
    writing-mode: vertical-lr;
    font-size: 2rem;
}

.main_header {
    padding: 30px 40px;
    width: 100%;
    border-bottom: 1px solid #010f1c;
}

.main_header .container {
    display: flex;
    justify-content: space-between;
}

.main_header .container .search_box {
    margin-right: 30px;
}

.main_header .container .search_box input {
    outline: none;
    padding: 10px 30px;
    border: 3px solid #0989ff;
    width: 300px;
    color: #010f1c;
}

.main_header .container .search_box button {
    border: 3px solid #0989ff;
    outline: none;
    padding: 10px 20px;
    cursor: pointer;
    background: #0989ff;
    color: #ecf2f7;
}

.main_header .container .icon {
    display: flex;
    margin-right: 50px;
}

.main_header .container .icon .account {
    display: flex;
    margin-right: 30px;
}

.main_header .container .icon .account .user_icon {
    margin-right: 10px;
    font-size: 22px;
    color: #010f1c;
    cursor: pointer;
}

.main_header .container .icon .account p {
    margin-right: 5px;
    margin-top: 2px;
}

.main_header .container .icon .second_icon {
    display: flex;
}

.main_header .container .icon .second_icon .link {
    margin-right: 5px;
    font-size: 22px;
    cursor: pointer;
    color: #0989ff;
}

.cart {
    position: relative;
}

.cart #cart-count {
    font-size: 20px;
    position: absolute;
    right: 0;
    top: -50%;

}

.header {
    width: 100%;
    padding: 20px 30px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.header .container {
    display: flex;
    max-width: 100%;
    justify-content: space-between;
}

.header .container .nav ul {
    display: flex;
}

.header .container .nav ul li {
    margin-left: 20px;
    list-style: none;
}

.header .container .nav ul li .link {
    text-decoration: none;
    color: #010f1c;
    transition: 0.5s;
}

.header .container .nav ul li .link:hover {
    color: #0989ff;
}

.header .container .auth {
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
}

.header .container .auth button {
    margin-right: 20px;
    font-size: 22px;
    cursor: pointer;
    outline: none;
    border: none;
    background: none;
}

.cart-notification {
    position: absolute;
    top: -10px;
    right: -10px;
    background-color: red;
    color: white;
    font-size: 12px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width:799px) {
    .logo .link {
        font-size: 1rem;
    }

    .main_header .container .search_box {
        margin-left: 30px;
    }

    .main_header .container .search_box input {
        padding: 7px 10px;
        width: 150px;
    }

    .main_header .container .search_box button {
        padding: 7px 10px;
        width: 70px;
    }
    .main_header .container .icon .account .user_icon {
    margin-right: 10px;
    font-size: 17px;
}

.main_header .container .icon .account p {
    margin-right: 5px;
    margin-top: 2px;
}

    .main_header .container .icon .second_icon .link {
        font-size: 18px;
    }
}

@media (max-width:477px) {
    .logo .link {
        font-size: 12px;
    }
    .main_header .container .search_box input {
        display: none;
    }
    .main_header .container .search_box button {
       display: none;
    }
    .main_header .container .icon .account {
        display: flex;
        margin-right: 0;
    }
    .main_header .container .icon .account .user_icon {
        margin-left: -35%;
    }
    .main_header .container .icon .account p {
        margin-right: 0;
        font-size: 12px;
    }
    .main_header .container .icon .second_icon {
        display: flex;
        margin-right: -30%;
    }
}